import React from "react";
import './geographic.css'
import { countryDataNew } from "data/countries2";

export const Geographic = props => {

    const areas = Object.entries(countryDataNew.area).map(([key, value]) => [key, typeof value === 'string' ? parseFloat(value.replace(',', '.')) : value]).sort(([keyA, valueA], [keyB, valueB])=> valueB - valueA);
    const population = Object.entries(countryDataNew.population).map(([key, value]) => [key, typeof value === 'string' ? parseFloat(value.replace(',', '.')) : value]).sort(([keyA, valueA], [keyB, valueB])=> valueB - valueA);
    return (
        <div className='rank'>
            <div>
                <h2>Areas</h2>
                {areas.map(([key, value], index) => <p key={index}>{key} - {value} km2</p>)}
            </div>
            <div>
            <h2>Population</h2>
                {population.map(([key, value], index) => <p key={index}>{key} - {value} mln</p>)}
            </div>
        </div>
    )
}
