import React from "react";

const Rank = ({sortedCountries}) => { // destukturyzacja i wyciagniecie klucza sortedCountries
    // funkcja genetujaca ranking krajów 
    const countryElement = (county, index) => ( 
        <div  key={'countryKey-'+index}>
            <p style={{fontSize: '1.5rem'}}><span>{index+1}.</span> {county['kraj']} - {county['pbj_globalny']}</p>
        </div>
    )
    return sortedCountries.map(countryElement)
}

export default Rank;